<template>
<div @click="clickBtn" class="bars">
    <div v-if="this.truth.active">
        <font-awesome-icon icon="bars" />
    </div>
    <div v-else>
        <font-awesome-icon icon="times" />
    </div>
</div>
</template>

<script>
export default {
    name: 'humberger',
    data() {
        return {
            truth: {
                active: true
            }
        }
    },
    methods: {
        clickBtn() {
            var nav = document.querySelector('#nav');
            if (this.truth.active) {
                nav.style.display = "block"
                this.$set(this.truth, 'active', false)
            } else {
                nav.style.display = "none"
                this.$set(this.truth, 'active', true)
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.bars {
    position: absolute;
    top: 3px;
    left: 3px;
    text-align: center;
    filter: drop-shadow(-1px -1px 1px var(--color-white)) drop-shadow(1px 1px 1px var(--color-shadow)) drop-shadow(-2px -2px 2px var(--color-white)) drop-shadow(2px 2px 2px var(--color-shadow));
    color: var(--color-bg);
    font-size: 32px;
    width: 50px;
    height: 50px;
    line-height: 50px;
    box-shadow: -1px -1px 1px var(--color-white), 1px 1px 1px var(--color-shadow);
}
</style>
