import Vue from 'vue'
import App from './App.vue'
import router from './router'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faHome,faUser,faChartBar,faBriefcase,faBlog,faBars,faTimes } from '@fortawesome/free-solid-svg-icons'
import { faTwitter,faFacebookF,faGithub} from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faHome,faUser,faChartBar,faBriefcase,faBlog,faBars,faTimes,faTwitter,faFacebookF,faGithub)

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
